import React, { useState, useEffect, Fragment } from "react";
import { connect, useSelector } from "react-redux";
import ProductCard from "../../components/features/product/";
import { addToCart } from "../../actions/cart";
import { searchApi, searchByMenu } from "../../api/products";
import Toolbox from "../../components/features/product/details/toolbox";
import QuickView from "../../components/features/product/QuickViewNew";
import ChooseVariationModal from "../../components/features/modal/choose-variation";
import { useTranslation } from "react-i18next";
import SimpleShimmerProducts from "../../components/features/loaders/SimpleShimmerProducts";
import InfiniteScroll from "react-infinite-scroll-component";
import ProductCardRowTwo from "../../components/features/product/ProductCardRowTwo";
import { Link } from "react-router-dom";
import { getMenuById } from "../checkout/helpers";
import { dt } from "../../i18n";

let column = "4cols";
const grid = {
	"2cols": "col-6",
	"3cols": "col-6 col-md-4 col-lg-4",
	"4cols": "col-6 col-md-4 col-lg-4 col-xl-3",
};
const initialPages = {
	page: 1,
	totalCount: 0,
	variations: [],
	hasMore: true,
};

const queryString = require("qs");

function ProductListSearch({ addToCart, title }) {
	const [cols, setCols] = useState(column);
	const [loading, setLoading] = useState(true);
	const [pages, setPages] = useState(initialPages);
	const [query, setQuery] = useState({ page: 1, size: 12 });
	const [menus, setMenus] = useState([]);
	const [modal, setModal] = useState({
		open: false,
		product: { variation: { id: 1 } },
	});
	const { menuList } = useSelector((state) => state.menu);
	const { t } = useTranslation();

	let mount = true;

	async function getProducts(query) {
		const strQuery = queryString.stringify(query);
		searchApi(title, strQuery)
			.then(async (page) => {
				if (mount) {
					setPages(() => ({
						page: page.page,
						totalCount: page.totalCount,
						variations: page.variations,
						hasMore: page.variations.length > 0,
						allPages: Math.ceil(page.totalCount / query.size),
					}));
					setLoading(false);
				}
			})
			.catch((err) => {
				setPages((prevPage) => ({
					...prevPage,
					hasMore: false,
				}));
				setLoading(false);
			});
	}
	async function fetchMoreData() {
		console.log(pages.variations.length);
		if (pages.totalCount < 13 || pages.variations.length > 12) {
			setPages((prevPage) => ({
				...prevPage,
				more_load_time: false,
				hasMore: false,
			}));
			return;
		}
		setQuery((q) => ({ ...q, size: 100 }));
	}

	useEffect(() => {
		setLoading(true);
		searchByMenu(title).then((res) => {
			setMenus(res.items);
		});

		return () => {
			setQuery({ page: 1, size: 12 });
			mount = false;
		};
	}, [title]);

	useEffect(() => {
		getProducts(query);
	}, [query]);

	function onAddToCart(product) {
		setModal({
			open: true,
			product: product,
		});
	}

	return (
		<>
			<ul className="result-menus mb-4">
				{menus.length > 0 && (
					<>
						<h2 className="title">{t("catalog")}</h2>
						<div className="menus">
							{menus.map((item) => (
								<div
									key={item.id}
									className={`menu-item ${
										menus.length < 2 ? `nomore` : `more`
									}`}
								>
									<>
										<ul className="menu-breadcrumb">
											{item.path
												.split(",")
												.slice(0, -2)
												.map((item) => (
													<Link
														to={`/category/${
															getMenuById(
																menuList,
																item
															)?.slug
														}`}
														className="breadcrumb-item"
														key={item}
													>
														{
															getMenuById(
																menuList,
																item
															)?.name
														}
													</Link>
												))}

											<Link
												to={`/category/${item.slug}`}
												className="breadcrumb-item"
												key={item}
											>
												{dt(item.name)}
											</Link>
										</ul>
									</>
								</div>
							))}
						</div>
					</>
				)}
			</ul>
			<div className="result-products">
				<h2 className="title">{t("products_two")}</h2>
				<Toolbox setCols={setCols} cols={cols} pages={pages} />
				{loading ? (
					<SimpleShimmerProducts />
				) : (
					<div className="products mb-3 d-flex flex-wrap">
						{pages.variations.length < 1 ? (
							<div className="row ml-auto mr-auto py-5">
								<h4>{t("nothing-found")}</h4>
							</div>
						) : (
							<Fragment>
								<InfiniteScroll
									dataLength={pages.variations.length}
									next={fetchMoreData}
									hasMore={pages.hasMore}
									loader={
										<div className="w-100">
											<SimpleShimmerProducts />
										</div>
									}
									className="row"
								>
									{pages.variations.map((product) => (
										<div
											className={`${grid[cols]}`}
											key={product.variation.id}
										>
											{cols === "12cols" ? (
												<ProductCardRowTwo
													product={product}
													type="sidebar"
													onAddToCart={(
														dataProduct
													) => {
														onAddToCart(
															dataProduct
														);
													}}
												/>
											) : (
												<ProductCard
													product={product}
													type="sidebar"
													onAddToCart={(
														dataProduct
													) => {
														onAddToCart(
															dataProduct
														);
													}}
												/>
											)}
										</div>
									))}
								</InfiniteScroll>

								<QuickView />
								<ChooseVariationModal
									setModal={setModal}
									modal={modal}
									addToCart={addToCart}
								/>
							</Fragment>
						)}
					</div>
				)}
			</div>
		</>
	);
}

export default connect(null, { addToCart })(ProductListSearch);
