import React, { useRef, useState } from "react";
import { Helmet } from "react-helmet";
import ProductListSearch from "./ProductListSearch";
import BreadCrumb from "../../components/common/breadcrumb";

function NoSideBar(props) {
	const slug = props.match.params.slug;
	const [crumb] = useState([
		{
			link: "#",
			name: "Поиск"
		},
		{
			link: `/search/${slug}`,
			name: slug
		}
	]);
	const myRef = useRef(null);
	const executeScroll = () =>
		myRef.current.scrollIntoView({ behavior: "smooth" });

	return (
		<>
			<Helmet>
				<title>Поиск {slug}</title>
			</Helmet>

			<h1 className="d-none">Поиск {slug}</h1>
			<BreadCrumb breadcrumb={crumb} />
			<div
				className="main"
				style={{
					position: "relative"
				}}
			>
				<div className="page-content search-result-page">
					<div className="container">
						<ProductListSearch
							title={slug}
							executeScroll={executeScroll}
						/>
					</div>
				</div>
			</div>
		</>
	);
}

export default React.memo(NoSideBar);
